.body {
  background-color: none; }

.portfolioContainer {
  width: 100%;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0px;
  left: 0;
  right: 0;
  margin-top: 50px;
  overflow-y: scroll;
  transform: scale(0.6);
  border-radius: 0px;
  border: 2px solid black; }

.portfolioContainerFull {
  width: 100%;
  height: 100%;
  margin: auto;
  top: 0;
  bottom: 0px;
  left: 0;
  right: 0;
  overflow-y: scroll;
  background-color: none; }

.portfolioBodyCont {
  position: absolute;
  background-color: none;
  width: 100%; }

.portfolioPage1 {
  width: 100%;
  height: 100%;
  background-color: none;
  position: absolute;
  color: #def2f1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.portfolioPage1Details {
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: left; }

.portfolioPage1SocialHolder {
  margin-top: 100px; }

.portfolioUserImage {
  border-radius: 120px;
  width: 200px;
  height: 200px; }

.portfolioUserDetails {
  margin-top: 4rem;
  color: #fff;
  font-size: 35px;
  margin: 40px; }

.portfolioUserName {
  font-weight: 900;
  letter-spacing: 1px; }

.portfolioUserLabel {
  font-weight: 600;
  letter-spacing: 2px; }

/* ------------------------------------------------------- */
.portfolioBtn {
  font-family: 'Cereal';
  font-weight: 600;
  width: 200px;
  text-align: center;
  cursor: pointer;
  font-weight: 600;
  height: 45px;
  background: linear-gradient(315deg, #091d37, #07182e);
  transition: 0.2s ease-out;
  color: #def2f1;
  line-height: 45px;
  border-radius: 15px; }

.portfolioBtn:hover,
.portfolioBtn:active {
  font-weight: 600;
  width: 200px;
  text-align: center;
  cursor: pointer;
  font-weight: 400;
  height: 45px;
  background: linear-gradient(315deg, #07182e, #091d37);
  transition: 0.2s ease-out;
  color: #def2f1;
  line-height: 45px;
  border-radius: 15px; }

.portfolioPage {
  width: 100%;
  height: auto;
  padding-bottom: 40px; }

/* ----------------------------------------------------------- */
.aboutPage {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-bottom: 0px; }

.portfolioPageSplit {
  width: 50%;
  min-height: 400px;
  height: auto; }

.leftAbout {
  width: 50%;
  background-color: none;
  color: #def2f1;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: center;
  font-size: 2em;
  font-weight: 700;
  margin-left: auto; }

.aboutCard {
  opacity: 0.8;
  background-color: #081b33;
  padding: 50px;
  border-radius: 20px;
  background: linear-gradient(45deg, #091d37, #07182e); }

.rightAbout {
  color: #def2f1;
  width: 60%;
  opacity: 0.8;
  background-color: #081b33;
  padding: 35px 20px;
  border-radius: 20px;
  background: linear-gradient(45deg, #091d37, #07182e);
  display: flex;
  justify-content: center;
  align-items: center; }

.portfolioUserSummary {
  width: 60%;
  text-align: justify;
  color: #fff; }

/* ---------------------------------------------------------- */
.workPage {
  background-color: none; }

.portfolioPageTitle {
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: #def2f1;
  text-align: center;
  padding-top: 40px;
  font-weight: 600;
  font-size: 26px;
  text-align: center;
  padding-bottom: 40px; }

.portfolioPage .portfolioEducationPage {
  background-color: none; }

.titleCard {
  padding: 10px;
  width: 200px;
  border-radius: 20px;
  background: #3e65ba;
  opacity: 0.8; }

.portfolioCardContainer {
  width: 80%;
  height: auto;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center; }

.portfolioCard {
  width: 300px;
  padding: 20px;
  min-height: 400px;
  height: auto;
  margin-top: 30px; }

.portfolioCardTitleMain {
  color: #d585a7 !important;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 500; }

.portfolioCardTitleSub {
  text-transform: capitalize;
  font-weight: 300;
  font-size: 18px; }

.miniCardLine {
  margin-right: auto;
  margin-left: 0px; }

.portfolioCardContent {
  width: 100%;
  text-align: justify; }

.portfolioCardWebsite {
  margin-top: 20px;
  font-size: 15px; }

.portfolioCardWebsite span {
  font-weight: 600;
  text-transform: uppercase; }

.portfolioCardWebsite a {
  color: #d585a7 !important;
  text-decoration: none; }

.portfolioCardDateContainer {
  width: 100%;
  height: 50px;
  border: 2px solid #000000;
  line-height: 50px;
  text-align: center;
  margin: auto;
  margin-top: 20px;
  font-weight: 600;
  background-color: #000000; }

/* -------------------------------------------------------------- */
.volunteerPage {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-bottom: 0px;
  background-color: none; }

.rightVolunteer {
  padding: 10px;
  border-radius: 20px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700; }

.rightVolunteerTitle {
  padding: 40px;
  border-radius: 20px;
  background: linear-gradient(45deg, #091d37, #07182e);
  opacity: 0.8;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700; }

.leftVolunteer {
  display: flex;
  width: 70%;
  flex-direction: column;
  background-color: none;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px; }

.portfolioVolunteerExpansionPanel {
  border-radius: 10px !important;
  color: #fff !important;
  background: linear-gradient(45deg, #091d37, #07182e);
