  font-weight: 600;
  text-transform: uppercase; }

.portfolioExpansionPanelSubTitle {
  color: #d585a7;
  font-weight: bold !important;
  font-size: 13px;
  text-transform: capitalize; }

.portfolioExpansionPanelDetails {
  border-radius: 10px !important;
  display: flex;
  flex-direction: column; }

.portfolioExpansionPanelWebsite {
  margin-top: 0px; }

.portfolioExpansionPanelWebsite span {
  font-weight: 600;
  text-transform: uppercase; }

.portfolioExpansionPanelWebsite a {
  color: #d585a7;
  text-decoration: none; }

/* ------------------------------------------------------------ */
.portfolioFlatContainer {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 20px;
  padding-bottom: 30px;
  background-color: none; }

.portfolioFlat {
  background-color: #191f67 !important;
  opacity: 0.8;
  color: #def2f1;
  width: 300px;
  min-height: 380px;
  height: auto;
  padding: 40px;
  border-radius: 20px;
  border-radius: 20px;
  transition: 0.2s ease-out; }

.portfolioFlat:hover {
  background-color: #d585a7 !important;
  color: white; }

.portfolioFlat:hover .portfolioFlatSubTitle {
  color: black; }

.portfolioFlat:hover .portfolioCardDateContainer {
  background-color: black; }

.portfolioFlatSubTitle {
  transition: 0.2s ease-out;
  color: #d585a7;
  text-transform: capitalize;
  font-weight: 400;
  margin-top: 10px; }

.portfolioFlatContentSummary {
  margin-top: 10px; }

/* ---------------------------------------------------- */
.extrasPage {
